
import React from 'react';
import Link from 'next/link';
import Image from 'next/image';

export default function IFAPage() {
    return (
        <main className="bg-white">
            {/* Hero Section */}
            <section className="relative bg-brand-dark py-20 lg:py-32 overflow-hidden text-white">
                <div className="absolute inset-0 bg-[url('/images/logo_origin.webp')] bg-no-repeat bg-right-top opacity-5 pointer-events-none mix-blend-overlay w-full h-full transform scale-150 translate-x-1/4 -translate-y-1/4"></div>
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10 text-center">
                    <span className="text-brand-gold font-bold uppercase tracking-widest text-sm mb-4 block">Our Solutions</span>
                    <h1 className="text-4xl md:text-5xl lg:text-6xl font-serif font-bold mb-6 text-white text-shadow-lg">Immediate Financing Arrangement (IFA)</h1>
                    <p className="text-xl text-slate-300 max-w-3xl mx-auto font-light leading-relaxed">
                        A Liquidity Strategy for Entrepreneurs and High-Net-Worth Families.
                    </p>
                </div>
            </section>

            {/* Introduction */}
            <section className="py-20 lg:py-24">
                <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
                    <p className="text-xl text-slate-600 leading-relaxed mb-8">
                        An Immediate Financing Arrangement (IFA) is a highly effective planning structure that allows business owners and incorporated professionals to retain liquidity while building long-term, tax-efficient wealth through a corporate-owned participating life insurance policy. It is designed for those who value both cash flow and the compounding advantages of permanent insurance.
                    </p>
                </div>
            </section>

            {/* Why Consider an IFA? */}
            <section className="py-20 bg-brand-light border-y border-slate-200">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-16 items-center">
                        <div className="relative h-[600px] w-full rounded-2xl overflow-hidden shadow-2xl order-2 lg:order-1">
                            <Image
                                src="/images/ifa-strategy.png"
                                alt="IFA Strategy"
                                fill
                                className="object-cover"
                            />
                            <div className="absolute inset-0 bg-brand-blue/10 mix-blend-multiply"></div>
                        </div>
                        <div className="order-1 lg:order-2">
                            <h2 className="text-3xl md:text-4xl font-serif font-bold text-brand-blue mb-10">Why Consider an IFA?</h2>

                            <div className="space-y-10">
                                <div className="flex">
                                    <div className="flex-shrink-0 mr-6">
                                        <div className="w-12 h-12 rounded-full bg-brand-gold flex items-center justify-center text-white font-bold text-xl shadow-md">1</div>
                                    </div>
                                    <div>
                                        <h3 className="text-xl font-bold text-brand-blue mb-2">Maintain Corporate Liquidity</h3>
                                        <p className="text-slate-600 leading-relaxed">
                                            Instead of tying up large premium payments within the corporation, an IFA allows you to borrow back the majority of the premium immediately through a bank line of credit secured against the policy’s cash value. Your corporate funds remain available for operations, investments, or new business opportunities.
                                        </p>
                                    </div>
                                </div>

                                <div className="flex">
                                    <div className="flex-shrink-0 mr-6">
                                        <div className="w-12 h-12 rounded-full bg-brand-gold flex items-center justify-center text-white font-bold text-xl shadow-md">2</div>
                                    </div>
                                    <div>
                                        <h3 className="text-xl font-bold text-brand-blue mb-2">Tax-Deductible Interest</h3>
                                        <p className="text-slate-600 leading-relaxed">
                                            For qualifying corporate structures, the interest paid on the loan may be deductible when the borrowed funds are used for income-generating business purposes. This creates an additional tax advantage compared to funding premiums directly.
                                        </p>
                                    </div>
                                </div>

                                <div className="flex">
                                    <div className="flex-shrink-0 mr-6">
                                        <div className="w-12 h-12 rounded-full bg-brand-gold flex items-center justify-center text-white font-bold text-xl shadow-md">3</div>
                                    </div>
                                    <div>
                                        <h3 className="text-xl font-bold text-brand-blue mb-2">Enhanced Long-Term Growth</h3>
                                        <p className="text-slate-600 leading-relaxed">
                                            The policy continues to accumulate cash value and grow on a tax-advantaged basis, regardless of the loan balance. This provides long-term stability, diversification, and an asset that is not correlated to market volatility.
                                        </p>
                                    </div>
                                </div>

                                <div className="flex">
                                    <div className="flex-shrink-0 mr-6">
                                        <div className="w-12 h-12 rounded-full bg-brand-gold flex items-center justify-center text-white font-bold text-xl shadow-md">4</div>
                                    </div>
                                    <div>
                                        <h3 className="text-xl font-bold text-brand-blue mb-2">Significant Estate Benefits</h3>
                                        <p className="text-slate-600 leading-relaxed">
                                            Upon the passing of the insured, the death benefit flows into the corporation and generates a Capital Dividend Account (CDA) credit. This allows funds to be paid out to shareholders tax-free, creating an exceptionally efficient legacy strategy.
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* How an IFA Works */}
            <section className="py-20 lg:py-24 bg-brand-dark text-white relative overflow-hidden">
                <div className="absolute inset-0 bg-white/5 opacity-10" style={{ backgroundImage: `radial-gradient(#ea8d22 1px, transparent 1px)`, backgroundSize: `40px 40px` }}></div>
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
                    <div className="text-center mb-16">
                        <h2 className="text-3xl md:text-5xl font-serif font-bold text-white mb-6">How an IFA Works</h2>
                        <p className="text-xl text-slate-400">A simplified process flow</p>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                        {/* Step 1 */}
                        <div className="bg-white/10 p-8 rounded-xl backdrop-blur-sm border border-white/10 hover:bg-white/20 transition duration-300">
                            <div className="text-brand-gold text-4xl mb-4 font-bold">01</div>
                            <h3 className="text-xl font-bold text-white mb-3">Apply for Policy</h3>
                            <p className="text-slate-300">Apply for a Corporate-Owned Participating Life Insurance Policy structured for long-term cash value growth.</p>
                        </div>

                        {/* Step 2 */}
                        <div className="bg-white/10 p-8 rounded-xl backdrop-blur-sm border border-white/10 hover:bg-white/20 transition duration-300">
                            <div className="text-brand-gold text-4xl mb-4 font-bold">02</div>
                            <h3 className="text-xl font-bold text-white mb-3">Pay the Premium</h3>
                            <p className="text-slate-300">The corporation funds the annual premium.</p>
                        </div>

                        {/* Step 3 */}
                        <div className="bg-white/10 p-8 rounded-xl backdrop-blur-sm border border-white/10 hover:bg-white/20 transition duration-300">
                            <div className="text-brand-gold text-4xl mb-4 font-bold">03</div>
                            <h3 className="text-xl font-bold text-white mb-3">Assign Collateral</h3>
                            <p className="text-slate-300">Assign the Policy as Collateral to a Partner Bank. The lender reviews the corporate financials and illustration.</p>
                        </div>

                        {/* Step 4 */}
                        <div className="bg-white/10 p-8 rounded-xl backdrop-blur-sm border border-white/10 hover:bg-white/20 transition duration-300">
                            <div className="text-brand-gold text-4xl mb-4 font-bold">04</div>
                            <h3 className="text-xl font-bold text-white mb-3">Receive Line of Credit</h3>
                            <p className="text-slate-300">Bank Advances a Line of Credit, typically up to 75%–90% of the policy’s cash value.</p>
                        </div>

                        {/* Step 5 */}
                        <div className="bg-white/10 p-8 rounded-xl backdrop-blur-sm border border-white/10 hover:bg-white/20 transition duration-300">
                            <div className="text-brand-gold text-4xl mb-4 font-bold">05</div>
                            <h3 className="text-xl font-bold text-white mb-3">Pay Interest Only</h3>
                            <p className="text-slate-300">Corporation Pays Interest Only. Interest may be tax-deductible depending on use of funds.</p>
                        </div>

                        {/* Step 6 */}
                        <div className="bg-white/10 p-8 rounded-xl backdrop-blur-sm border border-white/10 hover:bg-white/20 transition duration-300">
                            <div className="text-brand-gold text-4xl mb-4 font-bold">06</div>
                            <h3 className="text-xl font-bold text-white mb-3">Repeat Annually</h3>
                            <p className="text-slate-300">As premiums continue, cash value and loan capacity grow over time.</p>
                        </div>
                    </div>
                </div>
            </section>

            {/* Common Uses & Ideal Candidate */}
            <section className="py-20 lg:py-24 bg-brand-light">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-16">
                        <div>
                            <h3 className="text-3xl font-serif font-bold text-brand-blue mb-8">Common Uses of IFA for Business Owners</h3>
                            <ul className="space-y-4 text-lg">
                                <li className="flex items-start"><i className="fa-solid fa-check text-brand-gold mt-1.5 mr-3"></i>Expanding business operations</li>
                                <li className="flex items-start"><i className="fa-solid fa-check text-brand-gold mt-1.5 mr-3"></i>Acquiring real estate or equipment</li>
                                <li className="flex items-start"><i className="fa-solid fa-check text-brand-gold mt-1.5 mr-3"></i>Investing in new ventures</li>
                                <li className="flex items-start"><i className="fa-solid fa-check text-brand-gold mt-1.5 mr-3"></i>Building a diversified portfolio outside the corporation</li>
                                <li className="flex items-start"><i className="fa-solid fa-check text-brand-gold mt-1.5 mr-3"></i>Strengthening succession and estate planning</li>
                                <li className="flex items-start"><i className="fa-solid fa-check text-brand-gold mt-1.5 mr-3"></i>Funding buy–sell agreements or shareholder strategies</li>
                            </ul>
                        </div>
                        <div>
                            <h3 className="text-3xl font-serif font-bold text-brand-blue mb-8">Who Is an Ideal Candidate?</h3>
                            <p className="text-slate-600 mb-6">An IFA is most suitable for:</p>
                            <ul className="space-y-4 text-lg">
                                <li className="flex items-start"><i className="fa-solid fa-user-check text-brand-gold mt-1.5 mr-3"></i>Incorporated business owners</li>
                                <li className="flex items-start"><i className="fa-solid fa-user-check text-brand-gold mt-1.5 mr-3"></i>Professionals with a CCPC</li>
                                <li className="flex items-start"><i className="fa-solid fa-user-check text-brand-gold mt-1.5 mr-3"></i>Individuals with steady corporate cash flow</li>
                                <li className="flex items-start"><i className="fa-solid fa-user-check text-brand-gold mt-1.5 mr-3"></i>Those with long-term planning horizons</li>
                                <li className="flex items-start"><i className="fa-solid fa-user-check text-brand-gold mt-1.5 mr-3"></i>Clients with retained earnings seeking a more efficient structure</li>
                            </ul>
                            <div className="mt-8 p-4 bg-brand-gold/10 border-l-4 border-brand-gold rounded-r-lg">
                                <p className="font-bold text-brand-blue italic">"This strategy is especially compelling for clients who want to preserve liquidity today while creating a meaningful tax-free legacy in the future."</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* Risks & Considerations */}
            <section className="py-20">
                <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="bg-slate-50 border border-slate-200 p-10 rounded-xl">
                        <h2 className="text-2xl font-serif font-bold text-brand-blue mb-6 border-b border-slate-200 pb-4">Risks and Considerations</h2>
                        <ul className="space-y-3 mb-8 text-slate-700">
                            <li className="flex items-start"><i className="fa-solid fa-triangle-exclamation text-brand-gold mt-1 mr-3"></i>Interest rate fluctuations may impact annual loan costs</li>
                            <li className="flex items-start"><i className="fa-solid fa-triangle-exclamation text-brand-gold mt-1 mr-3"></i>Bank due diligence and documentation requirements</li>
                            <li className="flex items-start"><i className="fa-solid fa-triangle-exclamation text-brand-gold mt-1 mr-3"></i>Commitment to making ongoing premium and interest payments</li>
                            <li className="flex items-start"><i className="fa-solid fa-triangle-exclamation text-brand-gold mt-1 mr-3"></i>Need for long-term planning and strong cash flow management</li>
                        </ul>
                        <p className="font-bold text-brand-blue">
                            IFA is a strategy that should be implemented with guidance from experienced advisors, bankers, accountants, and tax professionals.
                        </p>
                    </div>
                </div>
            </section>

            {/* CTA Section */}
            <section className="py-20 lg:py-24 bg-brand-blue text-white text-center relative">
                <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
                    <h2 className="text-3xl md:text-5xl font-serif font-bold mb-8">Why Work With 99 Financial?</h2>
                    <p className="text-xl text-slate-300 mb-10">We specialize in insurance-based corporate tax planning for entrepreneurs, professionals, and high-net-worth families across Canada.</p>

                    <div className="grid grid-cols-2 md:grid-cols-4 gap-6 text-sm md:text-base font-bold text-brand-gold mb-12">
                        <div className="p-4 bg-white/5 rounded-lg border border-white/10">CDA maximization strategies</div>
                        <div className="p-4 bg-white/5 rounded-lg border border-white/10">Corporate-owned participating life insurance design</div>
                        <div className="p-4 bg-white/5 rounded-lg border border-white/10">IFA bank coordination and underwriting support</div>
                        <div className="p-4 bg-white/5 rounded-lg border border-white/10">Long-term tax-efficient wealth transfer</div>
                    </div>

                    <p className="text-lg text-white mb-10 bg-brand-gold/20 inline-block px-8 py-4 rounded-full border border-brand-gold/40">
                        Our role is to help you structure the strategy correctly, coordinate with lenders, and ensure your planning aligns with your corporate goals.
                    </p>

                    <div>
                        <a href="https://tidycal.com/greatec/30-minute-meeting" target="_blank" className="inline-block bg-brand-gold text-white font-bold text-xl py-5 px-12 rounded-sm hover:bg-white hover:text-brand-blue transition transform hover:-translate-y-1 shadow-xl">
                            Book an IFA Strategy Call
                        </a>
                    </div>
                </div>
            </section>
        </main>
    );
}
